.home {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro {
  max-width: 540px;
  text-align: center;
}

.home__img {
  margin-bottom: 1.5rem;
}

.home__name {
  font-size: var(--h1-font-size);
  font-weight: var(--font-bold);
  margin-bottom: 0.5rem;
}

.home__socials {
  display: flex;
  justify-content: center;
  margin: 1.5rem 0;
}

.home__socials > *:not(:last-child) {
  margin-right: 1.7rem;
}

@keyframes socials-enter {
  0% {
    opacity: 0;
    transform: translateY(15px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.home__social-link {
  color: var(--title-color);
  font-size: 1.3rem;
  transition: 0.3s;
  cursor: pointer;

  opacity: 0;
  animation: socials-enter 0.6s ease-out forwards;
}

/* stagger effect: each link appears a bit later */
.home__socials a:nth-child(1) {
  animation-delay: 0.2s;
}
.home__socials a:nth-child(2) {
  animation-delay: 0.4s;
}
.home__socials a:nth-child(3) {
  animation-delay: 0.6s;
}
.home__socials a:nth-child(4) {
  animation-delay: 0.8s;
}

.home__social-link:hover {
  color: hsl(43, 100%, 68%);
}

.icon {
  width: 22px;
  height: 22px;
  margin-top: 4px;
  fill: var(--title-color);
  transition: fill 0.3s;
}

.home__social-link:hover .icon {
  fill: hsl(43, 100%, 68%);
}

.scroll__down {
  position: absolute;
  bottom: 2.5rem;
  left: 0;
  width: 100%;
}

.home__scroll-name {
  font-size: var(--small-size);
}

.mouse {
  border: 2px solid var(--title-color);
  display: block;
  height: 1.6rem;
  width: 1.25rem;
  margin: auto;
  margin-top: 0.75rem;
  border-radius: 1rem;
  position: relative;
}

.mouse__wrapper {
  text-decoration: none;
  color: inherit;
  cursor: default;
}

@keyframes ani-mouse {
  0% {
    top: 29%;
  }

  15% {
    top: 50%;
  }

  50% {
    top: 50%;
  }

  100% {
    top: 29%;
  }
}

.wheel {
  background-color: var(--title-color);
  border-radius: 100%;
  width: 0.25rem;
  height: 0.25rem;
  position: absolute;
  top: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
  animation: ani-mouse 2s linear infinite;
}

.shapes {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -2;
}

.shape {
  position: absolute;
}

.s1 {
  left: 2%;
  top: 10%;
}

.s2 {
  left: 18%;
  top: 30%;
}

.s3 {
  left: 5%;
  bottom: 30%;
}

.s4 {
  left: 2%;
  bottom: 10%;
}

.s5 {
  left: 44%;
  top: 10%;
}

.s6 {
  left: 36%;
  bottom: 10%;
}

.s7 {
  top: 20%;
  right: 25%;
}

.s8 {
  right: 24%;
  bottom: 20%;
}

.s9 {
  right: 2%;
  top: 10%;
}

.s10 {
  right: 11%;
  top: 45%;
}

.s11 {
  right: 2%;
  bottom: 10%;
}

/* ========== responsive =========== */
@media screen and (max-width: 1024px) {
  .home__social-link {
    left: -110px;
  }
}

@media (max-width: 768px) {
  .scroll__down {
    display: none;
  }
}
